const MAX_DAY = 7
const MAX_COURSE = 20
const MAX_CLASSROOM = 1000
const MAX_CAPACITY = 1000
const MAX_CLASS = 1000


export const handleDayChangeAction = (input, key, course, teacherData) => {
    var inputPattern = /^\d+$/;
    if (inputPattern.test(input)) {
        if (input > MAX_DAY) {
            let subjectData = resetSubjectTimetable(MAX_DAY, course)
            let teacherTimetable = resetTeacherTimetable(MAX_DAY, course, teacherData)
            return {
                type: constants.CHANGE_DAY_VALUE,
                value: MAX_DAY,
                key: key,
                subjectData: subjectData,
                teacherTimetable: teacherTimetable

            }
        } else if (input <= 0) {
            let subjectData = resetSubjectTimetable(1, course)
            let teacherTimetable = resetTeacherTimetable(1, course, teacherData)
            return {
                type: constants.CHANGE_DAY_VALUE,
                value: 1,
                key: key,
                subjectData: subjectData,
                teacherTimetable: teacherTimetable
            }
        } else {
            let subjectData = resetSubjectTimetable(input, course)
            let teacherTimetable = resetTeacherTimetable(input, course, teacherData)
            return {
                type: constants.CHANGE_DAY_VALUE,
                value: input,
                key: key,
                subjectData: subjectData,
                teacherTimetable: teacherTimetable
            }
        }
    } else {
        var temp = input.substring(0, input.length - 1)
        let subjectData = resetSubjectTimetable(temp, course)
        let teacherTimetable = resetTeacherTimetable(temp, course, teacherData)
        return {
            type: constants.CHANGE_DAY_VALUE,
            value: temp,
            key: key,
            subjectData: subjectData,
            teacherTimetable: teacherTimetable
        }
    }
}

export const handleCourseChangeAction = (input, key, day, teacherData) => {
    var inputPattern = /^\d+$/;
    if (inputPattern.test(input)) {
        if (input > MAX_COURSE) {
            let subjectData = resetSubjectTimetable(day, MAX_COURSE)
            let teacherTimetable = resetTeacherTimetable(day, MAX_COURSE, teacherData)
            return {
                type: constants.CHANGE_COURSE_VALUE,
                value: MAX_COURSE,
                key: key,
                subjectData: subjectData,
                teacherTimetable: teacherTimetable
            }
        } else if (input <= 0) {
            let subjectData = resetSubjectTimetable(day, 1)
            let teacherTimetable = resetTeacherTimetable(day, 1, teacherData)
            return {
                type: constants.CHANGE_COURSE_VALUE,
                value: 1,
                key: key,
                subjectData: subjectData,
                teacherTimetable: teacherTimetable
            }
        } else {
            let subjectData = resetSubjectTimetable(day, input)
            let teacherTimetable = resetTeacherTimetable(day, input, teacherData)
            return {
                type: constants.CHANGE_COURSE_VALUE,
                value: input,
                key: key,
                subjectData: subjectData,
                teacherTimetable: teacherTimetable
            }
        }
    } else {
        var temp = input.substring(0, input.length - 1)
        let subjectData = resetSubjectTimetable(temp, 1)
        let teacherTimetable = resetTeacherTimetable(day, temp, teacherData)
        return {
            type: constants.CHANGE_COURSE_VALUE,
            value: temp,
            key: key,
            subjectData: subjectData,
            teacherTimetable: teacherTimetable
        }
    }
}

