const resetSubjectTimetable = (day, course) => {
    let tempData = []
    if (day !== "" && course !== "") {
        for (var i = 0; i < parseInt(course); i++) {
            let tempRowJson = {}
            for (var j = 0; j <= parseInt(day); j++) {
                if (j === 0) {
                    tempRowJson['jieshu'] = "" + (i + 1) + ""
                }
                if (j === 1) {
                    tempRowJson['day1'] = "11"
                }
                if (j === 2) {
                    tempRowJson['day2'] = "21"
                }
                if (j === 3) {
                    tempRowJson['day3'] = "31"
                }
                if (j === 4) {
                    tempRowJson['day4'] = "41"
                }
                if (j === 5) {
                    tempRowJson['day5'] = "51"
                }
                if (j === 6) {
                    tempRowJson['day6'] = "61"
                }
                if (j === 7) {
                    tempRowJson['day7'] = "71"
                }
            }
            let tempArray = [tempRowJson]
            tempData = tempData.concat(tempArray)
        }
    }
    return tempData
}

const resetTeacherTimetable = (day, course, teacherData) => {
    //timetable
    let tempData = []
    if (day !== "" && course !== "") {
        for (var i = 0; i < parseInt(course); i++) {
            let tempRowJson = {}
            for (var j = 0; j <= parseInt(day); j++) {
                if (j === 0) {
                    tempRowJson['jieshu'] = "" + (i + 1) + ""
                }
                if (j === 1) {
                    tempRowJson['day1'] = "11"
                }
                if (j === 2) {
                    tempRowJson['day2'] = "21"
                }
                if (j === 3) {
                    tempRowJson['day3'] = "31"
                }
                if (j === 4) {
                    tempRowJson['day4'] = "41"
                }
                if (j === 5) {
                    tempRowJson['day5'] = "51"
                }
                if (j === 6) {
                    tempRowJson['day6'] = "61"
                }
                if (j === 7) {
                    tempRowJson['day7'] = "71"
                }
            }
            let tempArray = [tempRowJson]
            tempData = tempData.concat(tempArray)
        }
    }

    //timetableteacher id 
    let realData = []
    if (day !== "" && course !== "" && teacherData.length !== 0) {
        for (var x = 0; x < teacherData.length; x++) {
            let tempIdJson = {}
            tempIdJson['id'] = teacherData[x]['id1']
            tempIdJson['timetable'] = tempData
            let rowTwoD = [tempIdJson]
            realData = realData.concat(rowTwoD)
        }
    }
    return realData
}

